@use 'base'

//HOVER
@media (hover:hover)
.header
    a:hover
        background-position: left bottom
        border-color: transparent
        transition: all 0.4s ease
.contact-form
    button:hover
        background-position: left bottom
        border-color: transparent
        transition: all 0.4s ease
.form-success
    button:hover
        background-position: left bottom
        border-color: transparent
        transition: all 0.4s ease
.about-main
    .download-btn:hover
        background-position: left bottom
        border-color: transparent
        transition: all 0.4s ease
.project-img:hover
    opacity: 1
    transition: all 0.4s ease
//MENU HOVER EFFECTS
.nav-link:hover, .nav-link.active:hover, .nav-link.main-active:hover
    // color: base.$pinkish
    color: #FFFFFF
    text-color: #FFFFFF
    transition: all 0.4s ease
.nav-link:hover::before, .nav-link.active:hover::before, .nav-link.main-active:hover::before
    content: "$"
    color: #FFFFFF
    text-color: #FFFFFF
    display: inline
    transition: all 0.4s ease
